    .publish_sidebar_toggle {
        display: block;
        position: fixed;
        right: 0;
        transform: rotate(-180deg);
        top: 137px;
        display: flex;
        align-items: center;
        border: 1px solid #ccc;
        border-left: 0;
        background: #fff;
        border-radius: 0 36px 36px 0;
        padding: 10px;
        z-index: 999;
        transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
    }

    .publish_sidebar_toggle:hover {
        padding-left: 20px;
    }

    .publish_sidebar_toggle.active svg{
        transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
    }

    .publish_sidebar_toggle.active {
        padding: 10px 9px 10px 5px;
    }

    .publish_sidebar_toggle:hover {
        padding-right: 15px;
        padding-left: 5px;
    }

    .publish_sidebar_toggle.active svg {
        transform: rotate(-180deg);
    }

    .publish_sidebar_toggle.active + .ec_sidebar{
        left:15px;
    }

    .ec_sidebar.active {
        left: -15px;
        
    }

    .ec_sidebar {
        height: calc(100vh - 112px) !important;
        overflow-y: auto;
    }

    .ec_sidebar_inner {
        padding: 60px 0 30px;
        height: calc(100vh - 162px);
    }

    .aside-fixed.aside.aside-on .right_submenu {
        left: 70px;
    }

    .ec_sidebar {
        height: auto !important;
    }

    .campaign-details p {
        width: 90%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    .publish_sidebar_toggle svg {
        width: 15px;
        height: 15px;
    }

    .publish_sidebar_toggle{
        padding: 10px 9px 10px 10px;
    }

    .tcc_section{
        margin-bottom: 0;
    }

}

@media screen and (max-width:991px){
    

    .ec_content {
        width: 100%;
    }

    .ec_sidebar_inner {
        box-shadow: 0px 0px 8px rgb(0 0 0 / 12%);
    }

    .content_inner {
        padding-top: 10px;
    }

    .footer_action{
        position: relative;
    }

    

    .mobile_tab_inner {
        height: calc(100vh - 175px);
        background-size: auto 90%;
        position: relative;
        overflow: hidden;
    }

    .email_capmaign_preview .mobile_tab_inner .preview_screen {
        max-width: 240px;
        width: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    .email_capmaign_preview .preview_screen {
        /* max-width: 70%; */
        /* padding-bottom: 100%; */
        position: relative;
    }

    /* .preview_grid_column {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
    } */

}

@media screen and (max-width:767px){
    .embed_code textarea{
        overflow-y: scroll;
    }

    .email_capmaign_preview .preview_tab,
    .email_capmaign_preview .ec_preview_screen  {
        height: auto;
    }

    .ec_content.email_rightAlign .choose_content, 
    .ec_content.email_rightAlign .desktop_Mobile_preview {
        width: 100%;
    }

    .ec_content.email_rightAlign .desktop_Mobile_preview {
        margin-top: 20px;
    }

}

@media screen and (max-width:575px){
    .footer_action {
        flex-wrap: wrap;
    }
    .footer_action p, .footer_action a:nth-child(2) {
        margin-bottom: 15px;
    }

    .content_wrapper{
        height: auto;
    }

    .ec_content.email_rightAlign .choose_content {
        padding-bottom: 15px;
    }

    .ec_content.email_rightAlign .desktop_Mobile_preview{
        height: auto;
    }

    .ec_content.email_rightAlign .desktop_Mobile_preview .heading_left {
        margin-bottom: 0px;
    }

}

@media screen and (max-width:480px){

    .desktop_Mobile_preview .tab_header {
        flex-flow: column;
        justify-content: center;
    }

    .heading_left {
        position: relative;
        left: 0;
    }

    .desktop_Mobile_preview .tab_header .tab-switch {
        max-width: 100%;
        min-width: 100%;
    }

    .heading_left {
        position: relative;
        left: 0;
        display: block;
        margin-bottom: 15px;
        width: 100%;
    }
}

@media screen and (max-width:360px){
    .email_capmaign_preview .mobile_tab_inner .preview_screen {
